import React, { useRef } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { searchLessons } from '../../actions/lessonActions';

const Searchbar = ({ searchLessons }) => {
  const text = useRef('');

  const onChange = (e) => {
    searchLessons(text.current.value);
  };
  return (
    <div className='row'>
      <div className='col s12'>
        <div className='row'>
          <div className='input-field col s12'>
            <i className='material-icons prefix'>search</i>
            <input
              type='text'
              id='autocomplete-input'
              className='autocomplete'
              placeholder='Search Lessons...'
              ref={text}
              onChange={onChange}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

Searchbar.propTypes = {
  searchLessons: PropTypes.func.isRequired,
};

export default connect(null, { searchLessons })(Searchbar);
