import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import LessonItem from './LessonItem';
import Preloader from '../layout/Preloader';
import PropTypes from 'prop-types';
import { getLessons } from '../../actions/lessonActions';

const Lessons = ({ lesson: { lessons, loading, filtered }, getLessons }) => {
  useEffect(() => {
    getLessons();

    // eslint-disable-next-line
  }, []);

  if (loading || lessons === null) {
    return <Preloader />;
  }
  return (
    <ul className='collection'>
      {filtered !== null && !loading && filtered.length !== 0 ? (
        filtered.map((lesson) => (
          <LessonItem lesson={lesson} key={lesson._id} />
        ))
      ) : !loading &&
        (lessons.length === 0 ||
          (filtered !== null && filtered.length === 0)) ? (
        <p className='center'>No lessons to show...</p>
      ) : (
        lessons.map((lesson) => <LessonItem lesson={lesson} key={lesson._id} />)
      )}
    </ul>
  );
};

Lessons.propTypes = {
  lesson: PropTypes.object.isRequired,
  getLessons: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  lesson: state.lesson,
});

export default connect(mapStateToProps, { getLessons })(Lessons);
