import {
  GET_LESSONS,
  SET_LOADING,
  LESSONS_ERROR,
  SEARCH_LESSONS,
  GET_LESSON_BY_ID,
} from '../actions/types';

const initialState = {
  lessons: null,
  filtered: null,
  current: null,
  loading: false,
  error: null,
  sermon: null,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initialState, action) => {
  switch (action.type) {
    case GET_LESSONS:
      return {
        ...state,
        lessons: action.payload,
        loading: false,
      };
    case SET_LOADING:
      return {
        ...state,
        loading: true,
      };
    case LESSONS_ERROR:
      console.error(action.payload);
      return {
        ...state,
        error: action.payload,
      };
    case SEARCH_LESSONS:
      return {
        ...state,
        filtered: state.lessons.filter((lesson) => {
          const regex = new RegExp(`${action.payload}`, 'gi');
          return lesson.title.match(regex);
        }),
        loading: false,
      };
    case GET_LESSON_BY_ID:
      return {
        ...state,
        sermon: action.payload,
      };

    default:
      return state;
  }
};
