/* eslint-disable jsx-a11y/iframe-has-title */
import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { getLessonById } from '../../actions/lessonActions';

const Lesson = ({ match, lesson: { sermon }, getLessonById }) => {
  useEffect(() => {
    getLessonById(match.params.id);
    // eslint-disable-next-line
  }, []);

  return sermon !== null ? (
    <div className='center'>
      <h4>{sermon.title}</h4>
      {typeof sermon.videoURL !== 'undefined' ? (
        <div className='video-container'>
          <iframe
            src={sermon.videoURL}
            frameBorder='0'
            allowFullScreen
          ></iframe>
        </div>
      ) : (
        ''
      )}
      <p style={contentStyle}>{sermon.content}</p>
    </div>
  ) : (
    ''
  );
};

Lesson.propTypes = {
  lesson: PropTypes.object.isRequired,
  getLessonById: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  lesson: state.lesson,
});

const contentStyle = {
  whiteSpace: 'pre-line',
  align: 'none',
};
export default connect(mapStateToProps, { getLessonById })(Lesson);
