import {
  GET_LESSONS,
  SET_LOADING,
  LESSONS_ERROR,
  SEARCH_LESSONS,
  GET_LESSON_BY_ID,
} from './types';

// Get Lessons from server
export const getLessons = () => async (dispatch) => {
  try {
    setLoading();
    const res = await fetch('/api/lessons');
    const data = await res.json();

    dispatch({
      type: GET_LESSONS,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: LESSONS_ERROR,
      payload: err.response.data,
    });
  }
};

// Get Lesson by ID from server
export const getLessonById = (id) => async (dispatch) => {
  try {
    const res = await fetch(`/api/lessons/${id}`);
    const data = await res.json();

    dispatch({
      type: GET_LESSON_BY_ID,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: LESSONS_ERROR,
      payload: err.response.data,
    });
  }
};

// Search server Lessons
export const searchLessons = (text) => async (dispatch) => {
  try {
    setLoading();
    dispatch({
      type: SEARCH_LESSONS,
      payload: text,
    });
  } catch (err) {
    dispatch({
      type: LESSONS_ERROR,
      payload: err.response.data,
    });
  }
};

// Set loading to true
export const setLoading = () => {
  return {
    type: SET_LOADING,
  };
};
